* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  color: white;
  background-color: black;
}

.main {
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
}

.main-title {
  font-size: 5rem;
}

.item-container {
  font-size: 2rem;
}

.item {
  border: 1px solid white;
  border-radius: 0.9rem;
  padding: 0.5rem;
  margin: 1rem;
  text-align: center;
}
